.email-model{width:100%}.email-model .input-element{min-height:32px;padding:1px 8px}.email-model .input-element .tag-wrapper{display:inline-block;height:22px;margin:3px 8px 3px 0;padding:0 10px;background:#ebf8ff;border-radius:2px;cursor:default}.email-model .input-element .tag-wrapper.active{background:#e2eff9}.email-model .input-element .tag-wrapper .tag-text{margin-right:8px;font-size:12px;line-height:22px;color:#666;cursor:text}.email-model .input-element .tag-wrapper .remove-tag{font-size:12px;-webkit-transform:scale(.8);-ms-transform:scale(.8);transform:scale(.8);color:#666;cursor:pointer}.email-model .input-element .tag-wrapper .remove-tag:hover{color:#000}.email-model .input-element .label-wrapper{margin-left:-6px}.email-model .input-element .label-wrapper input{height:29px;line-height:29px;border:0;padding-left:4px}.email-model .input-element.disabled .tag-wrapper{background:#d9d9d9}.email-model .input-element.disabled .email-input{background:none}.email-list-model{margin:-10px -13px}.email-list-model .ans-scroller{width:230px;overflow-y:scroll}.email-list-model .ans-scroller .default-option-class{width:230px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.email-list-model .ans-option .default-option-class.active{background:#ebf8ff}